import React, { useState } from "react";

export default function TextForm(props) {
  const data = { name: 'John', age: 42 }
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    props.showAlert('Convert to uppercase', 'success')
  };
  const handleLowerClick = () => {
    let lowerText = text.toLowerCase();
    setText(lowerText);
    props.showAlert('Convert to Lowecase', 'success')
  };
  const handleClear = () => {
    let lowerText = '';
    setText(lowerText);
    props.showAlert('Clear', 'success')

  };
  const handleOnClick = (event) => {
    setText(event.target.value);
  };
  const downloadTxtFile = () => {
    const element = document.createElement("a");
    const file = new Blob([text], {
      type: "text/plain"
    });
    element.href = URL.createObjectURL(file);
    element.download = "myFile.txt";
    document.body.appendChild(element);
    element.click();
  }
  const speak = () => {

    let msg = new SpeechSynthesisUtterance();
  
    msg.text = text;
  
    window.speechSynthesis.speak(msg);
    props.showAlert('Speak', 'success')

  
  }
  const intoTitleCase = () => {

    let newText = text.split(" ").map((currentValue) => {

        let newText = currentValue[0].toUpperCase() + currentValue.slice(1);

        return newText;
        

    });

    setText(newText.join(" "));

}
  const [text, setText] = useState("Enter text here");
  return (
    <div>
      <div className="mb-3" style={{color: props.mode==='dark'?'white':'black'}}>
      <h1>{props.heading} </h1>
      <button className="btn btn-outline-primary btn-sm" onClick={handleUpClick} > Convert to uppercase </button>
      <button className="btn btn-outline-dark btn-sm mx-1" onClick={handleLowerClick}> Convert to Lowercase </button>
      <button className="btn btn-outline-dark btn-sm mx-1" onClick={handleClear}>Clear</button>
      <button type="submit" onClick={speak} className="btn btn-warning btn-sm mx-2 my-2">Speak</button>
      <button type="submit" onClick={intoTitleCase} className="btn btn-outline-warning btn-sm mx-2 my-2">Capitalize</button>
      <button className="btn btn-outline-success btn-sm"  onClick={downloadTxtFile}>Download txt</button>
      <textarea className="form-control mt-2" id="exampleFormControlTextarea1" rows="10" value={text} onChange={handleOnClick}  ></textarea>
      </div>
      <p className="mt-2" style={{color: props.mode==='dark'?'white':'black'}}>{text.split(" ").length} words and {text.length} Character</p>
      <p style={{color: props.mode==='dark'?'white':'black'}}>{0.008 * text.split(" ").length } Minute to read</p>
      {/* <pre>
             {JSON.stringify(data, null, 2)}
           </pre> */}
    </div>
  );
}
