function App() {
  const [alert, setAlert] = useState('null');
  const [color, setColor] = useState('null'); //switch
  const showAlert = (message, type) => {
    setAlert({
      msg :message,
      type: type
    })
    setTimeout(() => {
      setAlert(null);
    }, 1000);
  }
  const [mode, setMode] = useState('light')
  const toggleMode = () => {
    if(mode === 'light') {
      setMode('dark');
      document.body.style.backgroundColor= '#614295';
      showAlert('Dark mode has enable', 'success')
    }
    else {
      setMode('light')
      document.body.style.backgroundColor = '#fff';
      showAlert('Light mode has enable', 'success')
    }
  }
  //switch
  const toggleColor = (e) => {
    setColor(e.target.value);
    document.body.style.backgroundColor = color
  }
  return (
    <>
      <Navbar mode={mode}  toggleMode={toggleMode} toggleColor={toggleColor} color={color} />  
      <div className='container mt-3'>
      <Alert alert={alert}/>
      <TextForm showAlert={showAlert} heading='Enter the text to analyze below'/>
      </div>
    </>
    // <Router>
    // <div >
    //   <div className="container m-5">

    //   <Alert alert={alert}/>
    //     <Switch>
    //       <Route path="/about">
    //         <About />
    //       </Route>
    //       <Route path="/textform">
    //         <TextForm />
    //       </Route>
    //       <Route path="/state">
    //         <State/>
    //       </Route>
    //       <Route path='/props'>
    //         <Props/>
    //       </Route>
    //       {/* <Route path='/state'>
    //         <State/>
    //         </Route>
    //         <Route path='/props'>
    //         <Props/>
    //       </Route> */}
    //       <Route path="/">
    //         <Home/>
    //       </Route>
    //     </Switch>
    //   </div>
    // </div>
    // </Router>
  );
}

export default App;
